
import Vue from 'vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import MescrollVue from 'mescroll.js/mescroll.vue'
import { getImgActivityPicList, getMyImgActivityPicList, submitImgZan } from '@/api/detail'
export default {
name: 'shoppingCard',
components: {
Radio,
Group,
MescrollVue
},
data () {
return {
detail:'',
tabstatus:true,
id:'',
alllist:[],
leftlist:[],
rightlist:[],
allpage:{
page:1,
pagenum:10,
pagestatus:true,
pagetext:"加载中..."
},
allmylist:[],
leftmylist:[],
rightmylist:[],
mypage:{
page:1,
pagenum:10,
pagestatus:true,
pagetext:"加载中..."
},
wejion:true,
allstatus:0,
mystatus:0,
sharestatus:true,
loading:true,
mescroll: null, // mescroll实例对象
mescrollDown:{
use:false
}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
mescrollUp: { // 上拉加载的配置.
callback: this.upCallback, // 上拉回调,此处可简写; 相当于 callback: function (page, mescroll) { getListData(page); }

page: {
num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
size: 10 
},
isBounce:true,
htmlNodata: '<p class="upwarp-nodata">亲,没有更多数据了~</p>',
empty: {

tip: "暂无相关数据~" 
}
},
}
},
created () {
this.id = this.$route.query.id

},
methods: {
upCallback (page, mescroll) {

if(this.loading){
if(this.tabstatus){
if(this.allpage.pagestatus){
this.getlist();
}
}
else{
if(this.mypage.pagestatus){
this.getmylist();
}
}
}
mescroll.endErr(true);
},

getlist(){
this.loading = false
const postData = {
actid:this.id,
page:this.allpage.page,
pagenum:this.allpage.pagenum,
}
getImgActivityPicList(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.detail = res.data.data
this.loading = true
if(this.sharestatus==true){
this.getshare();
this.sharestatus = false
}
res.data.data.list.map((item,index)=> {
if(index%2==0){
this.leftlist.push(item)
}
else{
this.rightlist.push(item)
}
this.alllist.push(item)
});
this.allstatus = 1
if(res.data.data.totalpage/this.allpage.pagenum>this.allpage.page){
this.allpage.page = this.allpage.page*1+1
}
else{
this.allpage.pagestatus = false
this.allpage.pagetext = "暂无更多照片了"
}

if(this.alllist.length==0){
this.wejion = false
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},

getmylist(){
this.loading = false
const postData = {
actid:this.id,
page:this.mypage.page,
pagenum:this.mypage.pagenum,
}
getMyImgActivityPicList(postData)
.then(res => {
if (res.data.errorcode === 0) {
res.data.data.list.map((item,index)=> {
if(index%2==0){
this.leftmylist.push(item)
}
else{
this.rightmylist.push(item)
}
this.allmylist.push(item)
});
this.mystatus = 1
this.loading = true
if(res.data.data.totalpage/this.mypage.pagenum>this.mypage.page){
this.mypage.page = this.mypage.page*1+1
}
else{
this.mypage.pagestatus = false
this.mypage.pagetext = "暂无更多照片了"
}

if(this.allmylist.length==0){
this.wejion = false
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
morebtn(){
if(this.tabstatus){
if(this.allpage.pagestatus){
this.getlist();
}
}
else{
if(this.mypage.pagestatus){
this.getmylist();
}
}
},
goshowphoto(){
this.$router.push({path:"/showphoto"})
},

tabclick(index){
if(index==0){
this.tabstatus = true
if(this.allstatus==0){
this.getlist()
}
}
else{
this.tabstatus = false
if(this.mystatus==0){
this.getmylist()
}
}
},

zan(status,direction,index,id){
const postData = {
id:id
}
submitImgZan(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.$vux.toast.text("点赞成功", 'middle')
if(status=="all"){
if(direction=='left'){
this.leftlist[index].zannum = this.leftlist[index].zannum*1+1
this.leftlist[index].selfzan = 1
}
else{
this.rightlist[index].zannum = this.rightlist[index].zannum*1+1
this.rightlist[index].selfzan = 1
}
}
else{
if(direction=='left'){
this.leftmylist[index].zannum = this.leftmylist[index].zannum*1+1
this.leftmylist[index].selfzan = 1
}
else{
this.rightmylist[index].zannum = this.rightmylist[index].zannum*1+1
this.rightmylist[index].selfzan = 1
}
}
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
godetail(id){
this.$router.push({path:"/showphoto/workdetail",query:{"id":id,"actid":this.id}})
},
goupload(){
this.$router.push({path:"/showphoto/upload",query:{"id":this.id}})
},
getshare(){
const that = this
const usertitle = that.detail.title
const subtitle = that.detail.subtitle
const shareimg = that.detail.pic
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: usertitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/allworks?id='+that.id+'',
imgUrl: shareimg,

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: usertitle,
desc: subtitle,
link: process.env.BASE_SELF+'/weixin/index.html?#/showphoto/allworks?id='+that.id+'',
imgUrl: shareimg,

success: function (res) {

}
})
})
}
},
}



